<template>
  <!-- LOGIN FORM -->
  <!--===================================================-->
  <div class="login-content">
      <div class="cls-content panel">
        <div class="mar-ver pad-btm">
            <h3 style="font-size:20px;">I Share</h3>
        </div>
        <form>
            <div class="form-group">
                <el-input v-model="username" placeholder="请输入用户名"></el-input>
            </div>
            <div class="form-group">
                <el-input v-model="password" placeholder="请输入密码"></el-input>
            </div>
            <div class="checkbox pad-btm text-left">
                <el-checkbox v-model="checked">记住密码</el-checkbox>
            </div>
            <el-button type="primary" style="width:230px;" @click="login">登 录</el-button>
        </form>
        <div class="pad-all">
            <a href="javascript:;" @click="forgetPass" class="btn-link mar-rgt">忘记密码 ?</a>
            <a href="javascript:;" @click="registerAccount" class="btn-link mar-lft">注册帐号</a>
        </div>
      </div>
  </div>
  <!--===================================================-->
</template>

<script>
import configUrl from './apiConfig'

export default {
    data () {
        return {
            username: '',
            password: '',
            checked: false
        }
    },
    beforeRouteEnter(to, from, next) {
        let localStorage = window.localStorage;
        next(vm => {
            // 通过 `vm` 访问组件实例
            if (localStorage.getItem('login')) {
                vm.$router.push({name: 'map'})
            }
        })
    },
  methods: {
        login() {
        //if login success
        let localStorage = window.localStorage;
        localStorage.setItem('login', true);
        //this.$router.push({name: 'map'})
        this.$emit('loginSuccess');
        this.$http.get(configUrl.user, {
            username: this.username,
            password: this.password
        }).then(res => {
            console.log(res)
        })
        .catch(err => {
            console.log(err)
        })
        },
        forgetPass() {
            this.$emit('forgetPass')
        },
        registerAccount() {
            this.$emit('registerAccount')
        }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  a {
    text-decoration: none;
    color: #758697;
    outline: 0;
  }
  .login-content{
    padding: 20px 15px 15px;
    position: relative;

    .cls-content {
        width: 70%;
        min-width: 270px;
        margin: 0 auto;
        position: relative;
        background-color: transparent;
        border: 0;
        box-shadow: none;

        .el-checkbox__label {
          color: #758697;
        }

        .btn-link {
            font-weight: 400;
            border-radius: 0;
        }

        .text-left {
            text-align: left;
        }

        .pad-btm {
            padding-bottom: 15px;
        }
        .mar-ver {
            margin-top: 15px;
            margin-bottom: 15px;
        }

        .form-group {
            margin-bottom: 15px;

            .form-control {
            font-size: 13px;
            height: 100%;
            border-radius: 0;
            box-shadow: none;
            border: 1px solid #e9e9e9;
            transition-duration: .5s;
        }
        }

        .checkbox{
            position: relative;
            display: block;
            margin-top: 10px;
            margin-bottom: 10px;
        }

        .pad-all {
          padding: 15px;

          a {
              font-size: 12px;
          }
        }
    }
  }
</style>



