<template>
    <div class="map-con">
        <div class="v-modal" :class="{'modal-active': showModal}"></div>
        <div id="allmap"></div>
        <topbar ref="topbar"></topbar>
        <el-dialog
          :title="markerTitle"
          :visible.sync="dialogVisible"
          size="tiny"
          custom-class="map-dialog"
          :before-close="handleClose">
          <span>这是一段信息</span>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
        <!--edit dialog-->
        <el-dialog
            :title="messageTitle"
            :visible.sync="editDialogVisible"
            size="large"
            custom-class="edit-dialog map-dialog slideInDown"
            :modal=false
            @open="onOpenChatDialog"
            :before-close="handleClose">
            <div class="chatbox">
              <!--<span class="reciveMsg chatmsg clearfix">
                <span class="chat-msg-head" style="backgroud:url(./static/img/logo.png)"></span>
                <span class="chat-msg-content">etstststststs</span>
              </span>
              <span class="sendMsg chatmsg clearfix">
                <span class="chat-msg-content">1121234124134</span>
              </span>-->
            </div>
            <span slot="footer" class="dialog-footer">
              <div class="chatfoot">
                <el-input v-model="msgInput" placeholder="请输入内容"></el-input>
                <el-button @click="sendMsg" type="primary">发送</el-button>
              </div>
            </span>
        </el-dialog>
    </div>

</template>

<script>
import mapHelper from '../helper/mapHelper'
import topbar from './topBar'
import _ from 'lodash'
import utils from '../helper/utils'
import configUrl from './apiConfig'

import { mapState } from 'vuex'

const mockPath = './static/mock/'

export default {
  data () {
    return {
      dialogVisible: false, 
      msg: 'Welcome to Your Vue.js App',
      activeName: '',
      markerTitle: 'This is a marker',
      editDialogVisible: false,
      msgInput: '',
      messageTitle: '与XX对话中'
    }
  },
  components: {
    topbar: topbar
  },
  beforeRouteEnter(to, from, next) {
      let localStorage = window.localStorage;
      next(vm => {
        // 通过 `vm` 访问组件实例
        if (!localStorage.getItem('login')) {
            console.log(vm.$refs)
            vm.$refs.topbar.loginDialogVisible = true;
        }
      })
  },
  computed: mapState({
    showModal: state => state.common.showModal
  }),
  mounted: function() {
    const me = this
    this.mapHelper = new mapHelper({
      mapStyle: 'amap://styles/fresh',
      mapClickHandler(lnglat, pixel, target) {
        console.log(me.$refs.topbar)
      },
    })
    this.mapHelper.drawMap()
    //TODO: there need to add user marker
    //acitivity marker, user marker
    this.getActivityMakerList((markerList) => {
      _.each(markerList, (marker) => {
        me.mapHelper.addMarker(marker, me.bindEventToMarker)
      })
    })
    this.$store.dispatch('initMapHelper', {mapHelper: this.mapHelper})
  },
  destroyed: function() {
    this.mapHelper.destroy()
  },
  methods: {
    getUserMarekerList(cb) {
      const me = this
      this.$http.get(mockPath + 'marker.json')
        .then((res) => {
            console.log(res.data)
            cb(res.data.item)
            me.$store.dispatch('initMarkerList', {markerList: res.data.item})
        }).catch((err) => {
            me.$message('oops, there is a error');
            console.log(err);
      })
    },
    getActivityMakerList(cb) {
      const me = this
      this.$http.get(mockPath + 'marker.json')
        .then((res) => {
            console.log(res.data)
            cb(res.data.item)
            me.$store.dispatch('initMarkerList', {markerList: res.data.item})
        }).catch((err) => {
            me.$message('oops, there is a error');
            console.log(err);
      })
    },
    getMakerInfo(markerId, cb) {
      this.$http.get(mockPath + 'marker.json?markerId' + markerId)
        .then((res) => {
            cb(null, res)
        }).catch((err) => {
            cb(err)
      })
    },
    bindEventToMarker(marker) {
      marker.on('click', this.handleMarkerClick)
    },
    onOpenChatDialog(){
      const mapDialog = document.querySelector('.edit-dialog')
      const parentNode = mapDialog.parentNode
      console.log(parentNode)
      //need to get the to user information
      //this.clearMsg();

      this.getUserInfo();
      this.getChatMsg();
      utils.createCanvasBg(mapDialog, parentNode, 'editImageMask')
    },
    handleMarkerClick(e) {
      const me = this
      const markerId = e.target.getExtData()
      //clear the number of the msg num
      var contentDom = e.target.getContentDom()
      console.log(e.target)
      contentDom.firstChild.lastElementChild.style.display = 'none';
      //document.querySelector('.msg-num')
      //
      this.getMakerInfo(markerId, (err, res) => {
        if (err) {
          me.$message('这是一条消息提示');
        } else {
          console.log(res)
          me.editDialogVisible = true;  
          // res = {
          //   "pos": "[116.480983, 39.989628]",
          //   "id": "1",
          //   "name": "下午4点成都篮球场约起来",
          //   "label": "体育运动"
          // }
          // me.mapHelper.addInfoWindow(e.target, res, {
          //   afterOpen() {

          //   }
          // })
        }
      })
      //this.dialogVisible = true
      console.log(e.target.getExtData())
      //test RefreshMsgNum Funtion
      // let markerExtData = e.target.getExtData()
      // markerExtData.refreshMsgNum(9)
    },
    handleClose(done) {
      done()
      // this.$confirm('确认关闭？')
      //   .then(_ => {
      //     done()
      //   })
      //   .catch(_ => {})
    },
    getUserInfo(id) {
        this.$http.get(configUrl.user + `/${id}`).then(res => {
            console.log(res)
            this.messageTitle = `与${res.msg.username}对话中`
        })
        .catch(err => {
            console.log(err)
        })
    },
    createSendMsg(msgInput) {
      let parentNode = document.querySelector('.chatbox');
      let sendNode = utils.parseDom(`<span class="reciveMsg chatmsg clearfix">\
                                      <span class="chat-msg-head" style="backgroud:url(./static/img/logo.png)"></span>\
                                      <span class="chat-msg-content">${msgInput}</span>\
                                  </span>`)
      console.log(sendNode)
      parentNode.appendChild(sendNode[0])
    },
    createRecieveMsg(msg) {
      let parentNode = document.querySelector('.chatbox');
      let sendNode = utils.parseDom(`<span class="sendMsg chatmsg clearfix">\
                                      <span class="chat-msg-content">${msg}</span>\
                                    </span>`)
      console.log(sendNode)
      parentNode.appendChild(sendNode[0])
    },
    sendMsg() {
      this.createSendMsg(this.msgInput);
      //create msg
      this.$http.post(configUrl.messages + '/1/2', {
        fromId: '1',
        toId: '3',
        //category is message type
        //1: test, 2: image 3:video 4: mixin
        category: '1',
        message: this.msgInput
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
      this.msgInput = null
    },
    getChatMsg(){
      console.log('get chat msg')
      //formId/toId
      let msg = [{
        type: 'recieve',
        content: '11111'
      },{
        type: 'recieve',
        content: '22222'
      },{
        type: 'send',
        content: '33333'
      },{
        type: 'recieve',
        content: '44444'
      },{
        type: 'send',
        content: '55555'
      },{
        type: 'send',
        content: '66666'
      },{
        type: 'send',
        content: '777777'
      },{
        type: 'recieve',
        content: '88888'
      },]
      _.delay(() => {
        let parentNode = document.querySelector('.chatbox');
        parentNode.innerHTML = '';
        _.each(msg, (val, key) => {
          console.log(val)
          if (val.type == 'recieve') {
            this.createRecieveMsg(val.content)
          } else if (val.type == 'send') {
            this.createSendMsg(val.content)
          }
        })
      }, 150)
      this.$http.get(configUrl.messages + '/1/2').then(res => {
        console.log(res)
        let msg = res.msg;
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style lang = "scss">

.map-con{
    height: 100%;
    /* Iphone6 */
    @media screen and (min-width: 375px) {
      .info-window {
        width: 300px;
        height: 400px;
      }
    }
    /* Iphone6 Plus */
    @media screen and (min-width: 414px) {
      .info-window {
        width: 320px;
        height: 500px;
      }
      .info-bottom {
        top: 48% !important;
      }
    }

    .map-dialog{
      width: 90%;
      background: rgba(8, 7, 7, 0.74);
    }
    .el-message-box{
      width: 50%;
    }
    .modal-active {
      z-index: 199;
    }

    .content-container {
      position: relative;
      z-index: 99;
      border-radius: 5px;
      border: 1px solid #ffffff;

      .info-title {
        border-bottom: 1px solid #ffffff;
        height: 30px;
        line-height: 30px;
      }

      .info-content {
        display: flex;
        align-items: stretch;
        height: 80px;
        padding: 3px 8px;
        
        .left-img {
          flex: 1;
          background-size: cover;
        }

        .right-content {
          flex: 2;
          text-align: left;
          padding-left: 10px;
        }
      }
    }

    .info-bottom {
      position: absolute;
      width: 100%;
      height: 30px;
      left: -20px;
      top: 35%;
      background: url('../assets/sharp.png');
      background-repeat: no-repeat;
    }

    .mask {
      position: absolute;
      top: 0;
      filter: blur(3px);
      width: 100%;
      height: 100%;
    }

    .edit-dialog {
      height: 80%;

      .el-dialog__body {
        height: 80%;
      }

      .chatbox {
        height: 100%;
        padding: 5px;
        overflow: auto;


        .reciveMsg {
            float: left;

            .chat-msg-content {
                background: #416886;
            }
        }
        .sendMsg {
            float: right;

            .chat-msg-content {
                background: #20a0ff;
            }
        }

        .clearfix:after {
            display:block;
            content: '';
            clear:both;
            height: 0px;
        }
        .chatmsg {
          width: 70%;
          
          .chat-msg-head {
              width: 25px;
              height: 25px;
              background: #ffffff;
              display: inline-block;
              border-radius: 50%;
              vertical-align: middle;
          }
          .chat-msg-content {
            vertical-align: top;
            display: inline-block;
            min-height: 30px;
            height: auto;
            width: 80%;
            border-radius: 3px;
            color: #ffffff;
            font-size: 20px;
            margin-bottom: 5px;
            text-align: left;
            padding: 3px;
            word-wrap: break-word; 
          } 
        }
      }
      .chatfoot {
        
        .el-input {
          width: 80%;
        }
      }
    }
    /* reset element style*/
    .el-dialog__title {
      color: #ffffff;
    }
    .el-tabs__item.is-active {
        color: #20a0ff;
    }

    .el-tab-pane {
      color: #ffffff;;
    }
}

#allmap{
    width: 100%;
    height: 100%;
    margin: 0 auto;
}
/* element style*/

</style>



